/* MyTipPage.css - Modern Design */
.my-tip-page-wrapper {
  display: flex;
  max-width: 1200px;
  margin: 20px auto;
  gap: 20px;
  padding: 0 15px;
}

.my-tip-page {
  flex: 1;
  padding: 24px;
  font-family: var(--font-family);
  background-color: #fff;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-sm);
}

.my-tip-page .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(0,0,0,0.05);
}

.my-tip-page h2 {
  margin: 0;
  color: var(--text-primary);
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.5px;
}

.submit-button {
  padding: 10px 20px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.2s;
  box-shadow: 0 4px 12px rgba(255, 80, 0, 0.2);
}

.submit-button:hover {
  background-color: #e64a19;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(255, 80, 0, 0.3);
}

.no-tips {
  text-align: center;
  padding: 60px;
  font-size: 16px;
  color: var(--text-secondary);
  background-color: var(--card-bg);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-sm);
}

.tip-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.tip-item {
  padding: 24px;
  border-radius: var(--border-radius-lg);
  background-color: var(--card-bg);
  box-shadow: var(--shadow-sm);
  transition: all 0.3s;
  border: 1px solid #eee;
}

.tip-item:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-hover);
  border-color: var(--primary-color);
}

.tip-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.tip-header h3 {
  margin: 0;
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 700;
}

.status {
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.status-pending { background-color: #fff7e6; color: #fa8c16; border: 1px solid #ffe7ba; }
.status-approved { background-color: #f6ffed; color: #52c41a; border: 1px solid #b7eb8f; }
.status-rejected { background-color: #fff1f0; color: #f5222d; border: 1px solid #ffa39e; }

.tip-item-content-wrapper {
  display: flex;
  gap: 20px;
}

.tip-image-wrapper {
  position: relative;
  width: 120px;
  height: 120px;
  flex-shrink: 0;
}

.tip-image-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  border: 1px solid #eee;
}

.image-count-badge {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 2px 6px;
  border-radius: 10px;
  font-size: 10px;
  font-weight: bold;
}

.tip-main-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.tip-content {
  font-size: 14px;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.tip-price {
  font-size: 18px;
  font-weight: 700;
  color: #ff5000;
  margin-bottom: 8px;
}

.tip-link a {
  display: inline-block;
  color: var(--primary-color);
  text-decoration: none;
  font-size: 13px;
  font-weight: 500;
}

.tip-link a:hover {
  text-decoration: underline;
}

.tip-footer {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.time {
  font-size: 12px;
  color: #999;
}

.delete-btn {
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  font-size: 13px;
  padding: 4px 8px;
  border-radius: 4px;
}

.delete-btn:hover {
  color: #f5222d;
  background-color: #fff1f0;
}

/* Form Styles */
.tip-form {
  background-color: #f9f9f9;
  padding: 24px;
  border-radius: var(--border-radius-lg);
  margin-bottom: 30px;
  border: 1px solid #eee;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 14px;
  color: var(--text-primary);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.2s;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 80, 0, 0.1);
}

.form-group textarea {
  min-height: 100px;
  resize: vertical;
}

.image-uploader-area {
  border: 2px dashed #ddd;
  border-radius: 8px;
  padding: 20px;
  background-color: #fff;
  transition: all 0.2s;
}

.image-uploader-area:hover {
  border-color: var(--primary-color);
  background-color: #fffcf9;
}

.image-preview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 12px;
  margin-bottom: 12px;
}

.preview-item {
  position: relative;
  width: 100%;
  padding-bottom: 100%; /* 1:1 Aspect Ratio */
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #eee;
}

.preview-item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.remove-image-btn {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 14px;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-image-btn:hover {
  background-color: #f5222d;
}

.add-image-button {
  position: relative;
  width: 100%;
  padding-bottom: 100%; /* 1:1 Aspect Ratio */
  border-radius: 8px;
  border: 2px dashed #ccc;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  background-color: #fafafa;
}

.add-image-button:hover {
  border-color: var(--primary-color);
  background-color: #fff0e6;
}

.add-image-button .plus-icon {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 32px;
  color: #999;
}

.add-image-button .add-text {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  color: #666;
  width: 100%;
  text-align: center;
}

.upload-hint {
  text-align: center;
  color: #999;
  font-size: 12px;
  margin: 0;
}

.submit-btn {
  width: 100%;
  padding: 12px;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: background-color 0.2s;
}

.submit-btn:hover {
  background-color: #e64a19;
}
