.footer {
  background-color: var(--card-bg);
  border-top: 1px solid rgba(0,0,0,0.05);
  padding: 32px 24px;
  margin-top: auto;
  color: var(--text-secondary);
}

.footer-section {
  text-align: center;
  margin-bottom: 24px;
}

.footer-section h4 {
  margin: 0 0 12px;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.footer-section p {
  margin: 6px 0;
  font-size: 13px;
  cursor: pointer;
  transition: color 0.2s;
}

.footer-section p:hover {
  color: var(--primary-color);
}

.footer-bottom {
  text-align: center;
  color: #999;
  font-size: 12px;
  padding-top: 24px;
  border-top: 1px solid rgba(0,0,0,0.05);
}

@media (max-width: 768px) {
  .footer {
    padding: 24px 16px;
  }
}
