/* Compare Page Layout */
.compare-page {
  padding-bottom: 100px;
  background: #f5f7fa;
  min-height: 100vh;
  padding-top: 20px; /* Prevent header overlap */
}

/* Search Section */
.search-section {
  background: white;
  padding: 30px 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  margin-bottom: 24px;
  position: relative;
  z-index: 10;
}

.search-box {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  gap: 12px;
  padding: 0 24px;
}

.search-input {
  flex: 1;
  height: 48px;
  padding: 0 20px;
  border: 2px solid #eee;
  border-radius: 24px;
  font-size: 16px;
  transition: all 0.2s;
}

.search-input:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 4px rgba(255, 80, 0, 0.1);
}

.search-btn {
  height: 48px;
  padding: 0 32px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 24px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
}

.search-btn:hover {
  background: #e64a19;
  transform: translateY(-1px);
}

.search-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Main Container */
.compare-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  gap: 24px;
  align-items: flex-start;
}

/* Sidebar */
.filter-sidebar {
  width: 280px;
  flex-shrink: 0;
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.03);
  position: sticky;
  top: 24px;
}

.filter-group {
  margin-bottom: 24px;
  padding-bottom: 24px;
  border-bottom: 1px solid #eee;
}

.filter-group:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.filter-group h3 {
  font-size: 16px;
  font-weight: 700;
  margin: 0 0 16px 0;
  color: var(--text-primary);
}

.price-inputs {
  display: flex;
  align-items: center;
  gap: 8px;
}

.price-inputs input {
  width: 100%;
  height: 36px;
  padding: 0 8px;
  border: 1px solid #ddd;
  border-radius: 6px;
}

.platform-checks {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.platform-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 14px;
}

.facet-list {
  max-height: 300px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.facet-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: var(--text-secondary);
  cursor: pointer;
}

/* Main Content */
.main-content {
  flex: 1;
  min-width: 0;
}

.results-header {
  background: white;
  padding: 16px 24px;
  border-radius: 12px;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 12px rgba(0,0,0,0.03);
}

.results-count {
  font-weight: 700;
  color: var(--text-primary);
}

.sort-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}

.sort-select {
  padding: 6px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  color: var(--text-secondary);
}

/* Product List */
.product-list-view {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.product-row {
  background: white;
  border-radius: 12px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 24px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.03);
  transition: all 0.2s;
}

.product-row:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.06);
}

.p-img {
  width: 120px;
  height: 120px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9f9f9;
  border-radius: 8px;
}

.p-img img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.p-info {
  flex: 1;
  min-width: 0;
}

.p-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: 12px;
}

.p-title a {
  color: var(--text-primary);
  text-decoration: none;
  transition: color 0.2s;
}

.p-title a:hover {
  color: var(--primary-color);
}

.p-meta {
  display: flex;
  gap: 12px;
}

.mall-tag {
  padding: 4px 12px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 700;
  color: white;
}

.mall-10 { background: #bf0000; } /* Rakuten */
.mall-20 { background: #ff0033; } /* Yahoo */
.mall-40 { background: #FF9900; } /* Amazon */

.p-price-area {
  width: 150px;
  text-align: right;
}

.p-price {
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 4px;
}

.p-action {
  width: 140px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.btn-shop {
  display: block;
  text-align: center;
  padding: 10px;
  background: var(--primary-color);
  color: white;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 600;
  transition: background 0.2s;
}

.btn-shop:hover {
  background: #e64a19;
}

.compare-check {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 6px;
  cursor: pointer;
  font-size: 13px;
  color: var(--text-secondary);
  transition: all 0.2s;
}

.compare-check:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.pagination {
  margin-top: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}

.page-btn {
  padding: 8px 16px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 6px;
  cursor: pointer;
}

.page-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Floating Comparison Dock & Modal */
.floating-dock {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 -4px 20px rgba(0,0,0,0.1);
  padding: 16px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  animation: slideUp 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  border-top: 1px solid rgba(0,0,0,0.05);
}

@keyframes slideUp {
  from { transform: translateY(100%); }
  to { transform: translateY(0); }
}

.dock-content {
  display: flex;
  align-items: center;
  gap: 24px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.selected-items-row {
  display: flex;
  gap: 16px;
  flex: 1;
}

.dock-item {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  border: 1px solid #e1e4e8;
  position: relative;
  background: white;
  transition: all 0.2s;
}

.dock-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 4px;
}

.dock-item:hover {
  border-color: var(--primary-color);
  transform: translateY(-2px);
}

.remove-item-btn {
  position: absolute;
  top: -8px;
  right: -8px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ff4d4f;
  color: white;
  border: none;
  font-size: 14px;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.2s;
}

.dock-item:hover .remove-item-btn {
  opacity: 1;
}

.dock-actions {
  display: flex;
  gap: 12px;
}

.btn-clear {
  padding: 10px 20px;
  background: #f5f5f5;
  color: #666;
  border: none;
  border-radius: 20px;
  font-weight: 600;
  cursor: pointer;
}

.btn-compare-start {
  padding: 10px 32px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 20px;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0 4px 12px rgba(255, 80, 0, 0.3);
  transition: transform 0.2s;
}

.btn-compare-start:hover {
  transform: scale(1.05);
  background: #e64a19;
}

/* Comparison Modal Table */
.compare-modal-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(4px);
  z-index: 2000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
}

.compare-modal {
  background: white;
  width: 100%;
  max-width: 1200px;
  height: 80vh;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0,0,0,0.2);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: modalPop 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes modalPop {
  from { transform: scale(0.95); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.modal-header-row {
  padding: 20px 24px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header-row h2 {
  margin: 0;
  font-size: 20px;
  color: var(--text-primary);
}

.modal-table-container {
  flex: 1;
  overflow: auto;
  padding: 24px;
}

.compare-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.compare-table th, 
.compare-table td {
  padding: 16px;
  border-bottom: 1px solid #eee;
  text-align: left;
  vertical-align: top;
}

.compare-table th {
  width: 150px;
  background: #f9f9f9;
  font-weight: 600;
  color: var(--text-secondary);
  position: sticky;
  left: 0;
  z-index: 10;
  border-right: 1px solid #eee;
}

.compare-col {
  min-width: 250px;
  border-right: 1px solid #f5f5f5;
  position: relative;
}

.col-header {
  text-align: center;
}

.col-img {
  width: 120px;
  height: 120px;
  object-fit: contain;
  margin-bottom: 12px;
}

.col-title {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4;
  height: 40px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 8px;
}

.col-price {
  font-size: 20px;
  font-weight: 700;
  color: var(--primary-color);
}

.col-action {
  display: block;
  margin-top: 12px;
  background: var(--primary-color);
  color: white;
  padding: 8px;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
}

.col-remove {
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  font-size: 18px;
}

.col-remove:hover {
  color: #ff4d4f;
}
