.register-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--bg-color);
  padding: 20px;
  font-family: var(--font-family);
}

.register-container {
  background: var(--card-bg);
  padding: 40px;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  width: 100%;
  max-width: 480px;
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.register-container h2 {
  text-align: center;
  margin-bottom: 32px;
  color: var(--text-primary);
  font-size: 24px;
  font-weight: 800;
  letter-spacing: -0.5px;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 600;
}

.form-input {
  padding: 12px 16px;
  border: 1px solid #e1e4e8;
  border-radius: var(--border-radius-md);
  font-size: 15px;
  transition: all 0.2s;
  background: #f8f9fa;
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-color);
  background: white;
  box-shadow: 0 0 0 4px rgba(255, 80, 0, 0.1);
}

.register-button {
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 14px;
  border-radius: 30px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 12px;
  box-shadow: 0 4px 12px rgba(255, 80, 0, 0.2);
}

.register-button:hover {
  background-color: #e64a19;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(255, 80, 0, 0.3);
}

.register-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.error-message {
  background-color: #fff2f0;
  color: #ff4d4f;
  padding: 12px;
  border-radius: var(--border-radius-sm);
  margin-bottom: 20px;
  font-size: 14px;
  border-left: 4px solid #ff4d4f;
}

.success-message {
  background-color: #f6ffed;
  color: #52c41a;
  padding: 12px;
  border-radius: var(--border-radius-sm);
  margin-bottom: 20px;
  font-size: 14px;
  border-left: 4px solid #52c41a;
}

.register-footer {
  margin-top: 32px;
  text-align: center;
  font-size: 14px;
  color: var(--text-secondary);
  border-top: 1px solid #eee;
  padding-top: 24px;
}

.register-footer p {
  margin: 8px 0;
}

.link {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 600;
  margin-left: 4px;
  transition: color 0.2s;
}

.link:hover {
  color: #e64a19;
  text-decoration: underline;
}
